<template>
    <div class="AccountSettingSucceed">
      <HomePageTop  :TopTitle="TopTotle" class="bg_fff" ></HomePageTop>
      <div class="successfullyadded">
<!--        <img src="../../../assets/img/common/bedefeated.png" alt="">-->
        <img src="../../../assets/img/common/succeed.png" alt="">
        <p class="f34">添加成功</p>
      </div>
      <div class="public-btn mt60 mr30 ml30" @click="prev">返回</div>
    </div>
</template>

<script>
    import Vue from "vue"
    import HomePageTop from "../../common/HomePageTop";

    export default {
        name: "AccountSettingSucceed",
        components: {HomePageTop},
        data() {
            return {
              //像子组件传值
              TopTotle: {name: "添加状态",left: true},
            }
        },
        methods: {
          prev(){
            this.$router.go(-1)
          },
        },
        created() {


        }
    }

</script>

<style>
  .successfullyadded{
    width: 100%;
    height: 4.72rem;
    background: #fff;
    text-align: center;padding-top: 1.4rem;
  }
  .successfullyadded img{width: 1.28rem;margin-bottom: .32rem}

</style>



